.result {
  text-align: center;
  max-width: 50%;
  margin: 50px auto;
  }
  
.convertedText {
  text-align: left;
  padding: 20px;
  border: 1px solid #aaa;
  border-radius: 25px;
  -webkit-transition: background-color ease-in 0.1s;
  -o-transition: background-color ease-in 0.1s;
  transition: background-color ease-in 0.1s;
  height: 75%;
  width: 100%;
}

.convertedText:hover {
  background-color: #eee;
}

.optionButtons {
  padding: 15px 0;
}


